.main-container {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: #C0C0C0;
}
.logout{
  font-style: italic;
}

.login-form {
  display: grid;
  grid-template-rows: 1fr;
  width: 100%;
}

.form-input {
  width: 100%;
}

.form-container {
  padding: 10px;
  width: 35%;
  background-color: #ffffff;
  border-radius: 20px;
}

.login-button {
  width: 100%;
}

.button-container{
  display: grid;
  grid-template-rows: 1fr 1fr;
}

.facebook-login {
  width: 100%;
}

.google-login{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  width: 100%;
  text-align: center;
}

.company-logo {
  width: 30px;
  height: 30px;
  text-align: center;
}


